import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { AbstractControl, FormGroup } from '@angular/forms';
import { EditableFormComponent } from 'src/app/components/editable-form/editable-form.component';
import { PerfilDaoService } from 'src/app/dao/perfil-dao.service';
import { IIndexable } from 'src/app/models/base.model';
import { Perfil } from 'src/app/models/perfil.model';
import { PageFormBase } from 'src/app/modules/base/page-form-base';

@Component({
  selector: 'app-perfil-form',
  templateUrl: './perfil-form.component.html',
  styleUrls: ['./perfil-form.component.scss']
})
export class PerfilFormComponent extends PageFormBase<Perfil, PerfilDaoService> {
  @ViewChild(EditableFormComponent, { static: false }) public editableForm?: EditableFormComponent;

  constructor(public injector: Injector) {
    super(injector, Perfil, PerfilDaoService);
    this.form = this.fh.FormBuilder({
      nome: {default: ""},
      descricao: {default: ""},
      nivel: {default: ""},
      data_inicio: {default: ""},
      data_fim: {default: ""},
    }, this.cdRef, this.validate);
  }

  public validate = (control: AbstractControl, controlName: string) => {
    let result = null;

    if(['nome', 'descricao'].indexOf(controlName) >= 0 && !control.value?.length) {
      result = "Obrigatório";
    }  else if(['nivel'].indexOf(controlName) >= 0 && !control.value) {
    result = "Não pode ser zero.";
    } 

    return result;
  }

  public loadData(entity: Perfil, form: FormGroup): void {
    let formValue = Object.assign({}, form.value);
    form.patchValue(this.util.fillForm(formValue, entity));
  }

  public initializeData(form: FormGroup): void {
    form.patchValue(new Perfil());
  }

  public saveData(form: IIndexable): Promise<Perfil> {
    return new Promise<Perfil>((resolve, reject) => {
      const perfil = this.util.fill(new Perfil(), this.entity!);
      resolve(this.util.fillForm(perfil, this.form!.value));
    });
  }

  public titleEdit = (entity: Perfil): string => {
    return "Editando " + (entity?.nome || "");
  }
}

